import React, { useRef, useState } from 'react'
import { insertUserInfo, setIsLogged } from '../state/actionCreators'
import { useDispatch } from 'react-redux'
import { bindActionCreators } from 'redux'
import axios from 'axios'
const SERVER = "http://localhost:3001"
const Login = () => {
  axios.defaults.withCredentials=true;
  const dispatch = useDispatch()
  const insertUserInf = bindActionCreators(insertUserInfo,dispatch)
  const isLoogedInf = bindActionCreators(setIsLogged,dispatch)
  const userName = useRef(null)
  const password = useRef(null)
  const warning = useRef(null)
  const displayWarning = ()=>{
    warning.current.style.display="flex"
  }
  const logIn = async()=>{
    let data={
      userName:userName.current.value,
      password:password.current.value
    }

    await axios.post(`/api/login`,data).then((res)=>{
      console.log(res.data.succes);
      if(res.data.succes){
        insertUserInf({userName:userName.current.value,student:res.data.student,isLogged:true})
        isLoogedInf({isLooged:true})
      }else{
        displayWarning()
      }
    }).catch((err)=>{
      console.log(err);
    })
  }

  
  return (
    <>
        <div>
            <h5>Welcome to Uranus</h5>
            <p>Sign up by entering information below</p>
        </div>
        <input type='UserName' placeholder='UserName' ref={userName} />
        <input type='Password' placeholder='Password' ref={password} onKeyUp={
          (e)=>{
            if(e.key=="Enter"){
              logIn()
            }
          }
        }/>
        <p ref={warning} className="warning">Email or password is incorect</p>
        <button onClick={logIn}>Log In</button>
        <p>Already have an account?<span>Log in</span></p>
    </>
  )
}

export default Login