import React, { useRef } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import axios from 'axios'

const SERVER = "http://localhost:3001"

const SignIn = () => {
  axios.defaults.withCredentials=true
  const firstName = useRef(null)
  const userName = useRef()
  const lastName = useRef()
  const password = useRef()
  const role = useRef()
  const verifyPassword = useRef()
  const warning = useRef(null)
  const succes = useRef(null)
  const displayWarning = (message)=>{
    if(message){
      warning.current.innerText = message
    }
    warning.current.style.display="flex"
  }
  const displaySucces = (message)=>{
    if(message){
      succes.current.innerText = message
    }
    succes.current.style.display="flex"
  }
  const handleSignIn = async() =>{
    if(firstName.current.value!==""||lastName.current.value!==""||userName.current.value!==""||password.current.value!==""||verifyPassword.current.value!==""){
      if(password.current.value===verifyPassword.current.value){
        await axios.post(`/api/signin`,{firstName:firstName.current.value,
          lastName:lastName.current.value,
          userName:userName.current.value,
          role:(role.current.value=="student"),
          password:password.current.value
        }).then((res)=>{
          if(res.data.succes){
            displaySucces(res.data.message)
            lastName.current.value=""
            userName.current.value=""
            password.current.value=""
          }else{
            displayWarning(res.data.message)
          }
        }).catch((err)=>{
          console.log(err);
        })
      }else{
        console.log("the passwords are not the same")
        displayWarning()
      }
    }else{
      console.log("some feilds are empty")
    
    }
  }
  return (
    <>
        <div>
            <h5>Welcome to Uranus</h5>
            <p>Sign up by entering information below</p>
        </div>
        <div>
            <input type='text' placeholder='First Name' ref={firstName}/>
            <input type='text' placeholder='Last Name' ref={lastName}/>
        </div>
        <select ref={role}>
            <option value="student">Student</option>
            <option value="techer">Teacher</option>
        </select>
        <input type='UserName' placeholder='Username' ref={userName}/>
        <input type='Password' placeholder='Password' ref={password}/>
        <input type='Password' placeholder='Verify Password' ref={verifyPassword} onKeyUp={
          (e)=>{if(e.key=="Enter")handleSignIn()}
        }/>
        <p ref={warning} className="warning">The passwords don't match each other</p>
        <p ref={succes} className="succes"></p>
        <div>
          <input type='checkbox' /> 
          <label >Always remember me</label>
        </div>
        
        <button onClick={handleSignIn}>Sign Up</button>
        <p>Already have an account?<span> Log in</span></p>
    </>
  )
}

export default SignIn